import { bridgeStore } from '@bridgeStore/index';
import { BridgeStoreStateType } from '../types';

export const waitForBridgeStorageLoaded = (): Promise<BridgeStoreStateType> => {
  return new Promise((resolve) => {
    const currentState = bridgeStore.getState();

    if (currentState.isStorageLoaded) {
      resolve(currentState);
      return;
    }

    const unsubscribe = bridgeStore.subscribe((state) => {
      if (state.isStorageLoaded) {
        unsubscribe();
        resolve(state);
      }
    });
  });
};
